.account-container {
    position: relative;
    margin: 0 auto;
    width: 414px;
    height: 899px;
    left: -1px;
    top: -3px;
    background: #FBFBFD;
}

.account-union {
    width: 222.69px;
    height: 221px;
    position: absolute;
    left: 287px
}

.Account-header {
    position: absolute;
    z-index: 3;
}

.Account-background {
    position: absolute;
    width: 414px;
    height: 232px;
    left: 0px;
    top: 0px;

    background: linear-gradient(170.64deg, #15EAF8 -10.08%, #58A0FA 44.31%, #BB34FD 98.7%);
}

.account-icon-notification {

    position: absolute;
    left: 351px;
    top: 48px;

}

.icon-account-back {
    color: #fff;
    position: absolute;
    width: 9px;
    height: 14px;
    left: 30px;
    top: 51px;
}

.icon-account-bell {
    position: absolute;
    width: 28px;
    height: 30px;
    left: 351px;
    top: 48px;
    color: #fff;
}

.account-number-noti {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    text-align: center;
    width: 18px;
    height: 18px;
    left: 14px;
    top: -7px;
    border: 1px;
    border-radius: 50px;
    background: #C52AFE;
    color: #fff;
    font-size: 10px;
}

.Account-header-avatar {

    position: absolute;
    width: 64px;
    height: 64px;
    left: 33px;
    top: 95px;
    cursor: pointer;
    border-radius: 50%;
}

.Account-header-name {
    position: absolute;
    width: 122px;
    height: 18px;
    left: 122px;
    top: 106px;


    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 18px;
    /* identical to box height, or 90% */


    color: #FFFFFF;
}

.Account-header-number-id {
    position: absolute;
    width: 132px;
    height: 18px;
    left: 122px;
    top: 131px;


    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    /* identical to box height, or 112% */

    letter-spacing: -0.165px;

    color: #FFFFFF;
}

.account-balance {
    position: absolute;
    width: 375px;
    height: 127px;
    left: 20px;
    top: 186px;
    background: #FFFFFF;
    box-shadow: 0px 1px 10px rgba(112, 112, 112, 0.2);
    border-radius: 10px;
    z-index: 3;
}

.account-balance-infomation {
    position: absolute;
    width: 191px;
    height: 18px;
    left: 88px;
    top: 20px;


    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 18px;
    /* identical to box height, or 100% */

    text-align: center;

    color: #636363;
}



.number-money {
    position: absolute;
    width: 195px;
    height: 18px;
    left: 76px;
    top: 60px;


    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 18px;
    /* identical to box height, or 60% */

    text-align: center;
    letter-spacing: -0.4px;

    color: #C52AFE;
}

.diem {
    position: absolute;
    width: 42px;
    height: 18px;
    left: 258px;
    top: 63px;


    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    /* identical to box height, or 112% */

    text-align: center;
}

/* content */
.account-content {
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    text-align: center;
    letter-spacing: -0.3px;
    color: #636363;
    
}

.account-content-wrap {
    position: relative;
    width: 414px;
    height: 652px;
    padding: 330px 0 0 20px;
    overflow-y: scroll;
   
}

.account-content-wrap::-webkit-scrollbar {
    width: 4px;
  }

.account-content-wrap::-webkit-scrollbar-track {
    background-color: #DFF5FF;
  }

.account-content-wrap::-webkit-scrollbar-thumb {
    background-color: #32CBF9;
    border-radius: 20px;
  }

.account-icon {
    display: flex;
    align-items: center;
    height: 62px;
    width: auto;
    margin: 14px 0;
}

.account-wallet-img,
.account-setting-img,
.account-help-img,
.account-contact-img{
    margin-right: 20px;
}

.elipse-wallet,
.elipse-setting,
.elipse-help,
.elipse-contact {
    position: relative;
}

.contact-background {
    width: 60p;
    height: 60px;
}

    .contact-icon {
        position: absolute;
        top: 14px;
        left: 19px;
    }

.wallet-icon {
    position: absolute;
    top: 12px;
    left: 16px;
}

.help-icon {
    position: absolute;
    top: 14px;
    left: 14px;
}

.setting-icon {
    position: absolute;
    top: 12px;
    left: 13px;
}

.account-wallet-img-background {

    width: 60px;
    height: 60px;
}

.account-wallet-img--info,
.account-help-img-info,
.account-setting-img-info,
.account-contact-info {
    font-weight: 400;
font-size: 18px;
line-height: 18px;
/* identical to box height, or 100% */


letter-spacing: -0.3px;

color: #636363;
}

/* hr */

.account-hr {
    width: 375px;
    height: 0px;
    border: 1px solid #DDDDDD;
}

/* contact */

.account-contact-phone-number {
    text-align: left;
    margin-top: 4px;
    font-weight: 700;
font-size: 18px;
line-height: 18px;
/* identical to box height, or 100% */

letter-spacing: -0.3px;

color: #ED822E;
}

.account-contact-description {
    font-weight: 400;
font-size: 16px;
font-style: italic;
line-height: 20px;
/* or 125% */
height: 70px;
letter-spacing: -0.5px;
text-align: left;
color: #AEAEAE;
}